import './ConnectHero.css'
import connecthero from '../../images/connect.jpg'
import React from 'react'
import { Typography } from '@mui/material'

const ConnectHero = () => {
  return (
    <div id='connecthero_container'>
      <img id='connect-hero-video' className='img-fluid' src={connecthero} alt='' />
      <div id='centered1'>
        <Typography variant='h2' sx={{ color: 'white', pb: 5, display: { xs: 'flex', sm: 'flex', md: 'flex', lg: 'flex', xl: 'flex' } }} className='text-center'>GET CONNECTED</Typography>
      </div>
    </div>
  )
}

export default ConnectHero;