import './Phone.css'
import phone from '../../images/phone.png'
import apple from '../../images/apple.png'
import google from '../../images/google.png'
import React from 'react'
import { Button } from '@mui/material'
const Phone = () => {
  return (
    <div id='phone' className="container-fluid phone_bg">
   <div className="row sectionn">
    
   <div className="col-sm-6">
        <img src={phone} alt='' />
    </div>
    <div className="col-sm-6 text-center">
        <h1>APP COMING SOON</h1>
        <div className='sc'>
        <img id='phone_app_pics' src={apple} alt=''/></div>
        <div className='sc'><img id='phone_app_pics1' src={google} alt=''/>
        </div>
        <div className='app_buttons'>
            <Button variant='contained' disabled sx={{wordSpacing: 2, mr: 3, backgroundColor: 'purple'}}>APP STORE</Button>
            <Button variant='contained' disabled sx={{backgroundColor: 'purple'}}>GOOGLE PLAY</Button>
        </div>
    </div>
  </div>
</div>
  )
}

export default Phone