import './PastorHero.css'
import { Typography } from '@mui/material'
import React from 'react'
import book from '../../images/pastorvid.mp4'
const PastorHero = () => {
  return (
    <React.Fragment>
    <div id='connecthero_container'>
    <video id='videotest' autoPlay loop muted>
        <source src={book} type="video/mp4" />
      </video>
      <div id='centered1'>
        <Typography style={{fontFamily: 'Merriweather',}} variant='h2' sx={{ color: 'white', pb: 5, display: { xs: 'flex', sm: 'flex', md: 'flex', lg: 'flex', xl: 'flex' } }} className='text-center'>OUR PASTOR</Typography>
      </div>
    </div>
    </React.Fragment>
  )
}

export default PastorHero