import './SermonsHero.css'
import book from '../../images/pastorpreaching.jpg'
import React from 'react'
import { Typography } from '@mui/material'
const SermonsHero = () => {
  return (
    <React.Fragment>
    <div id='connecthero_container'>
      <img id='video' className='img-fluid' src={book} alt='' />
      <div id='centered'>
        <Typography variant='h2' sx={{ color: 'white', pb: 5, display: { xs: 'flex', sm: 'flex', md: 'flex', lg: 'flex', xl: 'flex' } }} className='text-center'>SERMONS</Typography>
      </div>
    </div>
    </React.Fragment>
  )
}

export default SermonsHero