import './ContactHero.css'
import contactus from '../../images/contactus.jpg'
import React from 'react'
import { Typography } from '@mui/material'
const ContactHero = () => {
  return (
    <div id='connecthero_container'>
    <img id='connect-hero-video' className='img-fluid' src={contactus} alt='' />
    <div id='centered1'>
      <Typography variant='h2' sx={{ color: 'white', pb: 5, display: { xs: 'flex', sm: 'flex', md: 'flex', lg: 'flex', xl: 'flex' } }} className='text-center'>CONTACT US</Typography>
    </div>
  </div>
  )
}

export default ContactHero