import './WorshipTimes.css'
import React from 'react'
import { Typography } from '@mui/material'
import clock from '../../images/clock.png'
import fit from '../../images/cayacc_fit.jpg'
const WorshipTimes = () => {
    return (
        <div className=" worship_bg text-center container">
            <div className="row">
                <div className="col worship_text">
                    <div>
                        <br/>
                        <br/>
                        <h2 className='font-def-heading1 text-center' style={{fontFamily: 'Merriweather',}}><strong>WORSHIP SERVICES</strong></h2>
                        <Typography variant='h5' sx={{pb: 5, display: { xs: 'flex', sm: 'flex', md: 'flex', lg: 'flex', xl: 'flex' } }} className='text-center'>JOIN US IN-PERSON OR ONLINE SUNDAY at 10:00AM EST</Typography>
                        <p style={{ textAlign: 'left' }} className='text-center'>
                            <div>
                            <img className='svg_icons' src={clock} alt='' />
                            </div>
                            <strong>Sunday Morning</strong>
                            <br />
                            <strong>10:00 AM </strong>
                            MORNING WORSHIP
                            <br />
                        </p>
                        <br/>
                        <div>
                        <p style={{ textAlign: 'left' }} className='text-center'>
                        <div>
                            <img className='svg_icons1' src={fit} alt='' />
                            </div>
                            <strong>Tuesday &nbsp;</strong>
                            <br />
                            <strong>7:00 PM </strong>
                            CAYACC F.I.T. Conversations
                            <br />
                            <br />
                        </p>
                        <br/>
                        </div>
                    </div>
                </div>

                {/* holds the picture */}
                <div className="col worship_pic">
                </div>


            </div>
        </div>
    )
}

export default WorshipTimes