import React from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import { Routes, Route } from 'react-router-dom';
import Pastor from './pages/Pastor/Pastor';
import Home from './pages/Home/Home';
import Church from './pages/Church/Church';
import Connect from './pages/Connect/Connect';
import Ministry from './pages/Ministry/Ministry';
import Schedule from './pages/Schedule/Schedule';
import Sermon from './pages/Sermon/Sermon';
import Prayer from './pages/Prayer/Prayer';
import Events from './pages/Events/Events';
import Contact from './pages/Contact/Contact';
import Store from './pages/Store/Store';
import Yolo from './pages/Church/Yolo';
function App() {
  return (
    <React.Fragment>
      <Navbar />
      <Routes>
        <Route children path='/' element={<Home />} />
        <Route children path='/pastor' element={<Pastor />} />
        <Route children path='/church' element={<Church />} />
        <Route children path='/connect' element={<Connect />} />
        <Route children path='/ministries' element={<Ministry />} />
        <Route children path='/schedule' element={<Schedule />} />
        <Route children path='/sermons' element={<Sermon/>} />
        <Route children path='/prayer' element={<Prayer/>} />
        <Route children path='/events' element={<Events/>} />
        <Route children path='/foot_in' element={<Contact/>} />
        <Route children path='/store' element={<Store/>} />
        <Route children path='/api/users' element={<Yolo/>} />
      </Routes>
      </React.Fragment>
  );
}

export default App;
