import './StoreHero.css'
import book from '../../images/book.png'
import React from 'react'
import { Typography } from '@mui/material'
const StoreHero = () => {
  return (
    <div id='connecthero_container'>
    <img id='connect-hero-video' className='img-fluid' src={book} alt='' />
    <div id='centered1'>
      <Typography variant='h2' sx={{ color: 'white', pb: 5, display: { xs: 'flex', sm: 'flex', md: 'flex', lg: 'flex', xl: 'flex' } }} className='text-center'>STORE</Typography>
    </div>
  </div>
  )
}

export default StoreHero