import './Pastor.css'
import Navbar from '../../components/Navbar/Navbar'
import PastorHero from '../../components/PastorHero/PastorHero'
import React from 'react'
import pass from '../../images/lalala.png'
import ChurchFooter from '../../components/ChurchInfoFooter/ChurchFooter'
import Footer from '../../components/Footer/Footer'
const Pastor = () => {
    return (
        <React.Fragment>
            <PastorHero />
            <div class="text-center pastor_bg">
  <div class="numm">
    <div class="col popp">
     <img src={pass} alt='' className='img-fluid'/>
    </div>
    <div class="col nextt">
      
  <h1>
PASTOR ANTHONY PAYTON
</h1>
<br></br>
<p><strong>Pastor Anthony Payton</strong>, is a native of Hattiesburg, Mississippi. He has served as Senior Pastor/Teacher of Come As You Are Community Church in Fort Wayne, Indiana, for twenty-seven years.
Anthony accepted his call to the ministry in 1986. He preached his first sermon on April 14, 1986, at True Love Missionary Baptist Church, in Fort Wayne, under the pastorate of the late Bishop Jesse White.
Led by the Holy Spirit in 1995, Pastor Payton accepted the responsibility of stewarding 12 people of the former Greater Come As You Are Missionary Baptist Church. Today, with a membership of over 600 people, the churches home is a 28,000 square foot state of the art sanctuary.

 Among other accomplishments are:</p>
• <strong>Founder of Anthony Payton Signature Shoes</strong>
<br></br>
• <strong>Author of “Leadership Reflections” and soon to be released “The Cusp”</strong>
<br></br>
•<strong> Named Pastor of The Year by The Martin Luther King Jr. Club of Fort Wayne IN</strong>
<br></br>
•<strong> The first African American President of the Indiana Southern Baptist Pastor’s Conference.</strong>
<br></br>
• <strong>Developer of multiple Commercial Real Estate projects.</strong>
    <br></br>
		• <strong>Founder and Host of F.I.T. Conversation</strong>
    <br></br>
		•<strong>Contract Counselor for Fort Wayne Community Schools and East Allen County Schools</strong>
    <br></br>
		•<strong>Mental Health Counselor for Park Center Inc.</strong>
    <br></br>
		•<strong>International Testimony Speaker for The Gideons</strong>
    <br></br>
		Pastor Payton travels the world sharing the good news of Christ and has helped plant churches in Brazil and the United States. He speaks very candidly of a life of addiction and of his deliverance. What Satan once meant to use for evil, God has now converted and uses for the good of His kingdom building here on earth.
		Pastor Payton married on June 28, 1986, to wife Sandra Kay. They are the parents of Zachary and Lydia and the grandparents of three wonderful girls, and an host of young people that he has and continue to mentor. He resides with his family in Fort Wayne, Indiana.

      </div>
  </div>
</div>
<ChurchFooter />
<Footer />

        </React.Fragment>
    )
}

export default Pastor;