import './ChurchHero.css'
import { Typography } from '@mui/material'
import React from 'react'
import hero from '../../images/MicrosoftTeams-image (30).png'
const ChurchHero = () => {
  return (
    <React.Fragment>
    <div id='church_container'>
      <img id='video1' className='' src={hero} alt='' />
      <div id='centered'>
        <Typography style={{fontFamily: 'Merriweather',}} variant='h2' sx={{ color: 'white', pb: 5, display: { xs: 'flex', sm: 'flex', md: 'flex', lg: 'flex', xl: 'flex' } }} className='text-center'>OUR CHURCH</Typography>
      </div>
    </div>
    </React.Fragment>
  )
}

export default ChurchHero