import './Events.css'
import EventsHero from '../../components/EventsHero/EventsHero'
import React from 'react'
import ChurchFooter from '../../components/ChurchInfoFooter/ChurchFooter'
import Footer from '../../components/Footer/Footer'
import pic1 from '../../images/PrayerWorks.jpeg'
import pic2 from '../../images/CItyWidePrayer.jpg'
import pic3 from '../../images/WeAreHiring.jpeg'
import vid2 from '../../images/MothersDay.mp4'
import vid1 from '../../images/FathersDay.mp4'
const Events = () => {
    return (
        <React.Fragment>
            <EventsHero />
            <br />
            <h1 className='text-center'>Get Latest News, Events, and Schedule For CAYACC!</h1>
            <br />
            <div className='events_bg'>
       <div className="container text-center card-outer">

{/* make a function that changes class below to 'column'
if the viewpoint is below a certain width */}
 
<div className="numm">
<div className="col sc">
    <img
        src={pic1}
        className="img-thumbnail"
        alt=""
    />
    <h5>PRAYER WORKS</h5>
</div>
<div className="col sc">
    <img
        src={pic2}
        className="img-thumbnail"
        alt=""
    />
    <h5>CITYWIDE PRAYER SERVICE</h5>
</div>
<div className="col sc">
    <img
        src={pic3}
        className="img-thumbnail"
        alt=""
    />
    <h3>WE ARE HIRING!</h3>
</div>
</div>
</div>
    </div>

            <br></br>
            <br></br>
            <hr></hr>
            <div className='events_bg'>
       <div className="container text-center card-outer">

{/* make a function that changes class below to 'column'
if the viewpoint is below a certain width */}
 <h1>Latest Videos</h1>
<div className="numm vidss">
    
<div className="col sc">
<video muted controls> 
        <source src={vid1} type="video/mp4" />
      </video>
    <h3>Father's Day Announcements</h3>
    <h3>6.18.2023</h3>
</div>
<div className="col sc">
<video muted controls>
        <source src={vid2} type="video/mp4" />
      </video>
    <h3>Mother's Day Announcements</h3>
    <h3>4.23.2023</h3>
</div>
</div>
</div>
    </div>
            <br />
            <ChurchFooter />
            <Footer />
        </React.Fragment>
    )
}

export default Events