import './Contact.css'
import ContactHero from '../../components/ContactHero/ContactHero'
import React from 'react'
import ChurchFooter from '../../components/ChurchInfoFooter/ChurchFooter'
import Footer from '../../components/Footer/Footer'
import { initializeApp } from "firebase/app";
import { getAnalytics } from "firebase/analytics";
import { getDatabase, ref, push, set } from "firebase/database";
import { useState } from 'react'

const Contact = () => {
    // Import the functions you need from the SDKs you need

// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries

// Your web app's Firebase configuration
// For Firebase JS SDK v7.20.0 and later, measurementId is optional
const firebaseConfig = {
  apiKey: "AIzaSyBz3BSiH69uBJB7SVh2PnyNbOmhUVIaJTw",
  authDomain: "cayacc-church-form.firebaseapp.com",
  projectId: "cayacc-church-form",
  storageBucket: "cayacc-church-form.appspot.com",
  messagingSenderId: "19702878899",
  appId: "1:19702878899:web:82de2ea3eefe714a8216c6",
  measurementId: "G-LVRYVFQ3BL"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);
const database = getDatabase(app);

const [firstName, setFirstName] = useState('');
const [prayer, setPrayer] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [number, setNumber] = useState('');
  const [submitted, setSubmitted] = useState(false);


  const handleSubmit = async(event) => {
    event.preventDefault();
    addUser(firstName, lastName, email, number, prayer);
    console.log('Form submitted:', { firstName, lastName, email, number, prayer });
    setSubmitted(true);
  }

  const addUser = (firstName, lastName, email, number, prayer) => {
    const usersRef = ref(database, 'users');
    const newUserRef = push(usersRef);
    set(newUserRef, {
      firstName,
      lastName,
      email,
      number,
      prayer
    });
  };
  
  return (
    <React.Fragment>
        <ContactHero />
        <div className='container w-50 c_test'>
      {submitted ? (
        <>

          
          <h2 className='text-center'>Thank you for submitting the form!</h2>
          <div class='check_test'> <svg class="checkmark" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 52 52"> <circle class="checkmark__circle" cx="26" cy="26" r="25" fill="none"/> <path class="checkmark__check" fill="none" d="M14.1 27.2l7.1 7.2 16.7-16.8"/>
</svg>
</div>
</>
        ) : (
          <>
        <h2>CAYACC CONTACT FORM</h2>

          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <label htmlFor="firstName">First Name*</label>
              <input
                type="text"
                className="form-control"
                id="firstName"
                value={firstName}
                onChange={(event) => setFirstName(event.target.value)}
                required
              />
            </div>
            <div className="form-group">
              <label htmlFor="lastName">Last Name*</label>
              <input
                type="text"
                className="form-control"
                id="lastName"
                value={lastName}
                onChange={(event) => setLastName(event.target.value)}
                required
              />
            </div>
            <div className="form-group">
              <label htmlFor="email">Email*</label>
              <input
                type="email"
                className="form-control"
                id="email"
                value={email}
                onChange={(event) => setEmail(event.target.value)}
                required
              />
            </div>
            <div className="form-group">
            <label htmlFor="number">Phone Number</label>
              <input
                type="text"
                className="form-control"
                id="number"
                onChange={(event) => setNumber(event.target.checked)}
              />
            </div>
            <br/>
            <label>Message?</label>
            <div class="input-group input-group-large">
                  <textarea class="form-control" aria-label="With textarea" aria-describedby="inputGroup-sizing-lg"  style={{height: "75px"}}  value={prayer} onChange={(event) => setPrayer(event.target.value)}></textarea>
                </div>
            <br/>
            <button type="submit" className="btn btn-primary">
              Submit
            </button>
          </form>
          </>
        )}
      </div>
        <ChurchFooter />
        <Footer />
        </React.Fragment>
  )
}

export default Contact