import './Hero.css'
import React from 'react'
import dog from '../Navbar/Navbar'
import { Button, Typography, Alert } from '@mui/material'

import vid from '../../images/newcayaccvideo.mp4'
import { useState, useEffect } from 'react'
const Hero = () => {
  const [isLive, setIsLive] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  const apikey = 'AIzaSyCglKLBeUCX3D1x4VGYvTA1VWQBK9eIkJA'
  useEffect(() => {
    const checkLivestream = async () => {
      try {
      const response = await fetch(
        `https://www.googleapis.com/youtube/v3/search?part=snippet&channelId=UCxwaC9XShBmkgr4m7UTiRKQ&type=video&eventType=live&key=${apikey}`
      );
      if (!response.ok) {
        throw new Error('test')
      }
      const data = await response.json();
      setIsLive(data.items.length > 0);
    } catch (error) {
        console.error('Error', error)
    }
  }
    checkLivestream();
    const interval = setInterval(checkLivestream, 60000);

    return () => clearInterval(interval);
  }, []);

  const handleClick = () => {
    if (isLive) {
      window.location.href = 'https://www.youtube.com/channel/UCxwaC9XShBmkgr4m7UTiRKQ/live';
    } else {
      setShowAlert(true);
    }
  };

  return (
    <React.Fragment>
    <div id='hero_container'>
      <video id='videotest' autoPlay loop muted onEnded={() => dog}>
        <source src={vid} type="video/mp4" />
      </video>
      <div id='centered'>
        <Typography variant='h1' sx={{ color: 'white', pb: 1, pt: 10, display: { xs: 'flex', sm: 'flex', md: 'flex', lg: 'flex', xl: 'flex' } }} className='hero_title_text' style={{fontFamily: 'Merriweather',}}>I AM CAYACC</Typography>
        <Typography variant='h5' sx={{ color: 'white', pb: 5, display: { xs: 'flex', sm: 'flex', md: 'flex', lg: 'flex', xl: 'flex' } }} className='text-center'>LOVING HIM BY SERVING YOU</Typography>
        <Typography variant='h5' sx={{ color: 'white', pb: 5, display: { xs: 'flex', sm: 'flex', md: 'flex', lg: 'flex', xl: 'flex' } }} className='text-center'>JOIN US IN-PERSON OR ONLINE SUNDAY at 10:00AM EST</Typography>
        <Button variant='contained' target='_blank' onClick={handleClick} sx={{ color: 'white', backgroundColor: '#aa824e', ":hover": {bgcolor: 'blue', color: 'white', display: { xs: 'flex', sm: 'flex', md: 'flex', lg: 'flex', xl: 'flex' } }}}>Watch LiveStream</Button>
        {showAlert && (
        <Alert sx={{mt: 5}} severity="error" onClose={() => setShowAlert(false)}>
          There is no live stream available at this time.
          <br/>
          Latest Sermon is available <a href='/sermons' style={{color: 'black'}}>here</a>.
        </Alert>
      )}
      </div>
    </div>
    </React.Fragment>
  )
}

export default Hero;