import './UpcomingEvents.css'
import num1 from './../../images/num1.png'
import num2 from './../../images/num2.png'
import num3 from './../../images/num3.png'
import React from 'react'

const UpcomingEvents = () => {
    return (
        <React.Fragment>
            <br />
            <br />
            <br />
        <div className='events_bg'>
           <h1 className='text-center'>Upcoming Events</h1>
           <h4 className='text-center'>Mark your calendars for these great events coming up.</h4>
           <div className="container text-center card-outer">

{/* make a function that changes class below to 'column'
if the viewpoint is below a certain width */}
<div className="numm">
    <div className="col">
        <img
            src={num1}
            className="img-thumbnail"
            alt=""
        />
        <h3>HEALTH FAIR</h3>
    </div>
    <div className="col">
        <img
            src={num2}
            className="img-thumbnail"
            alt=""
        />
        <h3>MENTAL HEALTH AWARENESS</h3>
    </div>
    <div className="col">
        <img
            src={num3}
            className="img-thumbnail"
            alt=""
        />
        <h3>CAYACC COMMUNITY DAY</h3>
    </div>
</div>
</div>
        </div>
        <br/>
        <br/>
        <br/>
        </React.Fragment>
    )
}

export default UpcomingEvents;