import './ScheduleHero.css'
import book from '../../images/schedulepic.jpg'
import { Typography } from '@mui/material'
import React from 'react'

const ScheduleHero = () => {
  return (
    <React.Fragment>
    <div id='hero_container1'>
      <img id='video' className='img-fluid' src={book} alt='' />
      <div id='centered1'>
        <Typography variant='h2' sx={{ color: 'white', pb: 5, display: { xs: 'flex', sm: 'flex', md: 'flex', lg: 'flex', xl: 'flex' } }} className='text-center'>SCHEDULE / CALENDAR</Typography>
      </div>
    </div>
    </React.Fragment>
  )
}

export default ScheduleHero